@import '../../styles/global';

.header {
    color: white;
    width: 100%;
    position: fixed;
    top: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    line-height: 40px;
    padding: 0 5%;
    background-color: $color-primary;
    z-index: 9999;
    & > nav {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    & ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }
    & li {
        position: relative;
        display: inline-block;
        margin-left: 10px;
    }
    & a {
        display: block;
        color: white;
        height: 40px;
        line-height: 40px;
    }
    & img {
        vertical-align: middle;
        margin-top: -2px;
        cursor: pointer;
    }
}

.logo {
    display: block;
    height: 30px;
    top: 5px;
    margin-right: 10px;
}
