$color-primary: #333;
$color-background: #eef2f7;
$color-border: #ddd;
$color-text-light: #666;
$color-text-dark: #333;
$color-link: #551A8B;
$color-button-green: #008000;
$color-error-background: #f18c85;
$color-error-border: #f00;

* {
    box-sizing: border-box;
    font-family: sans-serif;
}

html, body {
    margin: 0;
    padding: 0;
    background-color: $color-background;
}

a {
    text-decoration: none;
    cursor: pointer;
}

@mixin card($maxWidth: 700px) {
    width: 90%;
    max-width: $maxWidth;
    border: 1px solid $color-border;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto 5% auto;
    @media(min-width: 768px) {
        margin: 0 auto 40px auto;
    }
}

@mixin auth_form {
    @include card;
    padding: 40px 5%;
    & h1 {
        margin-top: 0px;
    }
    & input {
        display: block;
        width: 100%;
        border: 1px solid $color-border;
        height: 30px;
        margin-bottom: 20px;
        line-height: 30px;
        padding-left: 5px;
        font-size: 14px;
    }
    & label {
        display: block;
        color: #333;
        font-size: 12px;
        margin-bottom: 5px;
        &::after {
            content: ' *';
            color: red;
        }
    }
    & button[type=submit] {
        @include submit-button($color-button-green);
    }
    & .validation_err_msg {
        color: red;
        font-size: 12px;
        float: right;
        margin-top: -15px;
    }
    & .message {
        @include message($color-error-background, $color-error-border, white);
    }
}

@mixin submit-button($background-color) {
    background-color: $background-color;
    padding: 10px 30px;
    border-radius: 5px;
    color: white;
    font-size: 18px;
    cursor: pointer;
    outline: none;
    &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
    }
}

@mixin message($background-color, $border-color, $text-color) {
    width: 100%;
    max-width: 700px;
    background-color: $background-color;
    border: 1px solid $border-color;
    padding: 10px;
    color: $text-color;
    border-radius: 5px;
    margin: 40px auto 0px auto;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

